@font-face {
    font-family: "Inter";
    src:
        local("Inter Light"),
        local("Inter-Light"),
        url("../fonts/Inter/Inter-Light.woff2") format("woff2"),
        url("../fonts/Inter/Inter-Light.woff") format("woff"),
        url("../fonts/Inter/Inter-Light.ttf") format("truetype");
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

